const fs = require('fs')
const User = require('../models/users.js')

exports.addUser = (req,res) =>{
   let data = new User({
       "name":req.body.name,
       "email":req.body.email
   })
    const storeData = (data, path) => {
      try {
        fs.writeFileSync('./usersDetails.json', JSON.stringify(data))
      } catch (err) {
        console.error(err)
      }
    }
}

